@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&family=Proza+Libre:wght@400;700&display=swap');

:root {
  --font-header: 'Proza Libre', sans-serif;
  --font-body: 'Open Sans', sans-serif;

  --primary: #020122;
  --secondary: #748386; /* gray web */
  --accent: #FF521B;
  --accent-rgb: 255, 82, 27;

  --light: #ffffff;
  --box-shadow: 0 3px 5px rgba(0,0,0,.3);
  --border: 1px solid rgba(0,0,0,.2);
}

*, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
  font-family: var(--font-body);
  background-color: #eeeeee;
}

h1,h2,h3,h4 {
  font-family: var(--font-header);
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}




/* utilities */
.container {
  width: 100%;
  max-width: 1200px;
  margin: auto;
  padding: 0 1rem;
}

.btn {
  border: none;
  text-transform: uppercase;
  font-family: var(--font-header);
  font-weight: 700;
  cursor: pointer;
  transition: all ease 0.1s;
}

.btn-accent {
  background-color: var(--accent);
  color: var(--light);  
}

.btn-accent:hover,
.btn-accent:focus {
  background-color: var(--primary);
  color: var(--accent);
  box-shadow: 0 3px 5px rgba(0,0,0,.3);
}

.btn-accent:active {
  transform: translateY(.3rem);
}
 
.margin-y-sm {
  margin: 1rem;
}