@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&family=Proza+Libre:wght@400;700&display=swap);
:root {
  --font-header: 'Proza Libre', sans-serif;
  --font-body: 'Open Sans', sans-serif;

  --primary: #020122;
  --secondary: #748386; /* gray web */
  --accent: #FF521B;
  --accent-rgb: 255, 82, 27;

  --light: #ffffff;
  --box-shadow: 0 3px 5px rgba(0,0,0,.3);
  --border: 1px solid rgba(0,0,0,.2);
}

*, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
  font-family: 'Open Sans', sans-serif;
  font-family: var(--font-body);
  background-color: #eeeeee;
}

h1,h2,h3,h4 {
  font-family: 'Proza Libre', sans-serif;
  font-family: var(--font-header);
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}




/* utilities */
.container {
  width: 100%;
  max-width: 1200px;
  margin: auto;
  padding: 0 1rem;
}

.btn {
  border: none;
  text-transform: uppercase;
  font-family: 'Proza Libre', sans-serif;
  font-family: var(--font-header);
  font-weight: 700;
  cursor: pointer;
  transition: all ease 0.1s;
}

.btn-accent {
  background-color: #FF521B;
  background-color: var(--accent);
  color: #ffffff;
  color: var(--light);  
}

.btn-accent:hover,
.btn-accent:focus {
  background-color: #020122;
  background-color: var(--primary);
  color: #FF521B;
  color: var(--accent);
  box-shadow: 0 3px 5px rgba(0,0,0,.3);
}

.btn-accent:active {
  transform: translateY(.3rem);
}
 
.margin-y-sm {
  margin: 1rem;
}

.header {
   /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#131313+0,4c4c4c+100 */
    background: #131313; /* Old browsers */ /* FF3.6-15 */ /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to bottom,  #131313 0%,#4c4c4c 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#131313', endColorstr='#4c4c4c',GradientType=0 ); /* IE6-9 */

}

.main-nav {
    text-align: center;
}

.main-nav ul {
    display: flex;
    justify-content: space-evenly;
    padding: 0 0 1rem 0;
}

.main-nav ul a {
    color: var(--light);
}

.main-nav ul a:hover,
.main-nav ul a:focus {
    color: var(--accent);
}

.main-nav .logo {
    font-size: 3rem;
    font-family: var(--font-header);
    color: var(--light);
    text-transform: uppercase;
    padding: 1rem 0;
}

@media (min-width: 768px) {
    .main-nav {
        display: flex;
        justify-content: space-between;
        align-items: center;
    } 

    .main-nav ul a { 
        padding-left: 3rem;
    }
    
}
.add-form > * {
    display: block;
    width: 100%;
    margin: 1rem 0;
    padding: 1rem;
    font-family: var(--font-body);    
    border: var(--border);
    border-radius: 5px;
}

.add-form > *:focus {
    outline: none;
    border: none;
    border: 1px solid rgba(var(--accent-rgb), 0.5);
}


@media (min-width: 768px) {
    .add-form {
        display: flex;
        justify-content: space-between;
        grid-column-gap: 1rem;
        -webkit-column-gap: 1rem;
                column-gap: 1rem;
        padding: 2rem 0;
    }
    .add-form select[name="activity"]{
        width: calc(30% - 1rem);
    }

    .add-form select[name="owner"]{
        width: calc(30% - 1rem);
    }

    
    .add-form input[type="text"]{
        width: calc(100% - 1rem);
    }

    .add-form input[type="submit"]{
        width: calc(20% - 1rem);
    }
}

@media (min-width: 768px) {
    .search-form.add-form > * {
        width: calc(33.33% - 1rem);
    }
}
@media (min-width: 768px) {
  .task-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    grid-row-gap: 0.5rem;
    row-gap: 0.5rem;
  }
  .task-list > * {
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: calc(50% - 1rem);
    margin: 0 0.5rem;
  }
}

@media (min-width: 900px) {
  .task-list > * {
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: calc(33.33% - 1rem);
    margin: 0 0.5rem;
  }
}
.task-item {
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  margin-bottom: 1rem;
  padding: 3rem 2rem;
  font-size: 1.3rem;
  background-color: var(--light);

  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.task-item__head {
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  padding-bottom: 1rem;
  display: flex;
  justify-content: space-between;
}

.task-item__body {
  text-align: center;
  padding: 2rem 1rem;
}

select:invalid {
  color: gray;
}

.task-item__foot {
  border-top: 1px solid rgba(0, 0, 0, 0.2);
  margin-top: 2rem;
  padding-top: 1rem;
  font-style: italic;

  display: flex;
  justify-content: space-between;
}

.task-delete {
  cursor: pointer;
  color: var(--primary);
}

.task-delete:hover {
  color: var(--accent);
}

.table {
    width: 100%;
}

.table  * {
    border: 1px solid rgba(0,0,0,.2);
    padding: .8rem;
}

.thead-dark tr th {
    background-color: var(--secondary);
    color: white;
}

tbody tr:nth-child(even) {
    background-color: #ccc;
}

tbody tr:hover{
    background-color: var(--light);
}
