@media (min-width: 768px) {
  .task-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    row-gap: 0.5rem;
  }
  .task-list > * {
    flex: 0 0 calc(50% - 1rem);
    margin: 0 0.5rem;
  }
}

@media (min-width: 900px) {
  .task-list > * {
    flex: 0 0 calc(33.33% - 1rem);
    margin: 0 0.5rem;
  }
}
.task-item {
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  margin-bottom: 1rem;
  padding: 3rem 2rem;
  font-size: 1.3rem;
  background-color: var(--light);

  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.task-item__head {
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  padding-bottom: 1rem;
  display: flex;
  justify-content: space-between;
}

.task-item__body {
  text-align: center;
  padding: 2rem 1rem;
}

select:invalid {
  color: gray;
}

.task-item__foot {
  border-top: 1px solid rgba(0, 0, 0, 0.2);
  margin-top: 2rem;
  padding-top: 1rem;
  font-style: italic;

  display: flex;
  justify-content: space-between;
}

.task-delete {
  cursor: pointer;
  color: var(--primary);
}

.task-delete:hover {
  color: var(--accent);
}
