
.header {
   /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#131313+0,4c4c4c+100 */
    background: #131313; /* Old browsers */
    background: -moz-linear-gradient(top,  #131313 0%, #4c4c4c 100%); /* FF3.6-15 */
    background: -webkit-linear-gradient(top,  #131313 0%,#4c4c4c 100%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to bottom,  #131313 0%,#4c4c4c 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#131313', endColorstr='#4c4c4c',GradientType=0 ); /* IE6-9 */

}

.main-nav {
    text-align: center;
}

.main-nav ul {
    display: flex;
    justify-content: space-evenly;
    padding: 0 0 1rem 0;
}

.main-nav ul a {
    color: var(--light);
}

.main-nav ul a:hover,
.main-nav ul a:focus {
    color: var(--accent);
}

.main-nav .logo {
    font-size: 3rem;
    font-family: var(--font-header);
    color: var(--light);
    text-transform: uppercase;
    padding: 1rem 0;
}

@media (min-width: 768px) {
    .main-nav {
        display: flex;
        justify-content: space-between;
        align-items: center;
    } 

    .main-nav ul a { 
        padding-left: 3rem;
    }
    
}