.add-form > * {
    display: block;
    width: 100%;
    margin: 1rem 0;
    padding: 1rem;
    font-family: var(--font-body);    
    border: var(--border);
    border-radius: 5px;
}

.add-form > *:focus {
    outline: none;
    border: none;
    border: 1px solid rgba(var(--accent-rgb), 0.5);
}


@media (min-width: 768px) {
    .add-form {
        display: flex;
        justify-content: space-between;
        column-gap: 1rem;
        padding: 2rem 0;
    }
    .add-form select[name="activity"]{
        width: calc(30% - 1rem);
    }

    .add-form select[name="owner"]{
        width: calc(30% - 1rem);
    }

    
    .add-form input[type="text"]{
        width: calc(100% - 1rem);
    }

    .add-form input[type="submit"]{
        width: calc(20% - 1rem);
    }
}

@media (min-width: 768px) {
    .search-form.add-form > * {
        width: calc(33.33% - 1rem);
    }
}