.table {
    width: 100%;
}

.table  * {
    border: 1px solid rgba(0,0,0,.2);
    padding: .8rem;
}

.thead-dark tr th {
    background-color: var(--secondary);
    color: white;
}

tbody tr:nth-child(even) {
    background-color: #ccc;
}

tbody tr:hover{
    background-color: var(--light);
}